function randomMot(array) {
  return array[Math.floor(Math.random() * array.length)];
}
function getRandomInt(max) {
  return Math.floor(Math.random() * Math.floor(max));
}
function creerPhrase() {
  let phrase = '';
  if (getRandomInt(2) === 1) {
    phrase += ` ${randomMot(listNom.feminin)}`;
    phrase += ` ${randomMot(listVerb)}`;
    phrase += ` ${randomMot(listAdjective.feminin)} `;
  } else {
    phrase += ` ${randomMot(listNom.masculin)}`;
    phrase += ` ${randomMot(listVerb)}`;
    phrase += ` ${randomMot(listAdjective.masculin)} `;
  }

  // eslint-disable-next-line no-useless-concat
  phrase += '. ';
  return phrase;
}

const nLignes = document.getElementById('nbLignes');

let count = 1;
document.getElementById('btn').addEventListener('click', () => {
  if (nLignes.value > 0) {
    const article = document.createElement('article');
    const head = document.createElement('h4');
    article.appendChild(head);
    for (let j = 0; j < nLignes.value; j++) {
      const p = document.createElement('p');
      p.textContent += creerPhrase();
      article.appendChild(p);
    }
    head.textContent = `Poeme numero ${count}`;
    article.classList.add(`article${count}`);
    document.getElementById('div2').appendChild(article);
    console.log(count);
    count++;
    nLignes.value = ' ';
  } else {
    // eslint-disable-next-line no-alert
    alert('Vous devez entrer le nombre de lignes!');
    nLignes.value = ' ';
  }
});
